.App {
  text-align: center;
  width: 100%;
}

.gameContainer {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: 75px 1fr;
  position: relative;
}

.imgContainer {
  grid-column: 2/6;
  grid-row: 2/3;
  position: relative;
  /* overflow: hidden; */
  width: 1600px;
}

.gameContainer img {
  width: 100%;
  object-fit: cover;
}

.sidebar {
  grid-column: 1/2;
  grid-row: 2/3;
  background-color: rgb(255, 196, 0);
  filter: drop-shadow(1px 4px 11px rgba(103, 103, 103, 0.35));
  display: flex;
  min-width: 300px;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: sticky;
  left: 0;
  z-index: 200;
  padding-top: 50px;
}

.box {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  border: 3px dashed rgb(0, 0, 0);
  position: absolute;
}

.popUpContainer {
  width: 150px;
  height: 200px;
  background-color: rgba(29, 29, 29, 0.938);
  position: absolute;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 5px 0;
}

.popUpContainer button {
  padding: 10px;
  width: 90%;
  border-radius: 5px;
  border: none;
  font-size: 14px;
}

.popUpContainer button:hover {
  background-color: rgb(95, 95, 95);
  color: white;
}

.characterImg {
  margin: 10px 0;
  background-color: white;
  width: 80%;
  border-radius: 30px;
  padding: 10px 0;
}

.characterImg img {
  height: 100px;
  width: 50px;
  object-fit: contain;
}

.navBar {
  grid-column: 1/6;
  grid-row: 1/2;
  background-color: rgb(253, 143, 0);
  position: sticky;
  width: 100%;
  height: 75px;
  top: 0;
  z-index: 300;
}

.dataContainer {
  display: flex;
  align-items: center;
  margin-left: 50px;
  gap: 50px;

  width: 300px;
  position: sticky;
  left: 50px;
}

.timerContainer p {
  font-size: 24px;
}

.found img {
  filter: grayscale();
  border: 3px solid red;
}

.found {
  color: red;
  text-decoration: line-through;
}

.overlay {
  width: 100%;
  height: 100vh;
  background-color: rgba(59, 59, 59, 0.808);
  grid-row: 1/3;
  grid-column: 1/6;
  z-index: 500;
}

.window {
  width: 600px;
  height: 300px;
  border-radius: 30px;
  background-color: rgb(253, 143, 0);
  margin: 200px auto 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.window input {
  padding: 10px;
  font-size: 16px;
  border-radius: 10px;
  border: 3px solid rgb(253, 80, 0);
}

.window button {
  font-size: 16px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  margin-top: 10px;
}

.navBar button,
.instructionsContainer button {
  font-size: 16px;
  padding: 10px;
  border-radius: 5px;
  border: none;
}

.disabled {
  background-color: rgb(179, 179, 179);
  color: rgb(212, 212, 212);
  pointer-events: none;
}

.inactive {
  display: none;
}

.menuContainer {
  background-color: rgb(253, 143, 0);
  margin: 200px auto 0 auto;
  width: 400px;
  display: flex;
  flex-direction: column;
  padding: 5px;
  align-items: center;
  border-radius: 20px;
}

.menuContainer button,
.scoreboardContainer button {
  font-size: 24px;
  padding: 15px;
  width: 200px;
  margin: 20px 0;
  border: none;
  border-radius: 10px;
}

.menuContainer button:hover,
.navBar button:hover,
.instructionsContainer button:hover,
.window button:hover,
.scoreboardContainer button:hover {
  background-color: rgb(105, 105, 105);
  color: white;
}

.instructionsContainer,
.scoreboardContainer {
  background-color: rgb(253, 143, 0);
  margin: 200px auto 0 auto;
  width: 500px;
  display: flex;
  flex-direction: column;
  padding: 20px 30px;
  align-items: center;
  border-radius: 20px;
}
.instructionsContainer p {
  margin-bottom: 40px;
}

form {
  display: flex;
  flex-direction: column;
}

.scoreContainer {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0 20px;
  background-color: rgb(255, 196, 0);
  border-radius: 15px;
  margin: 10px 0;
  align-items: center;
}

.playerContainer {
  display: flex;
  gap: 5px;
}

.foundBox {
  background-color: rgba(255, 0, 0, 0.575);
  border-style: solid;
}
